* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: Arial;           
}

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    /* padding: 0 2px;  */
}

/* Create four equal columns that sits next to each other */
.column {
    -ms-flex: 25% 1;
        flex: 25% 1;
    max-width: 25%;     
    /* padding: 0 2px; */
}

.image {
    height: 0;
    padding-bottom: 100%;
    position: relative;
    width: 100%;
    overflow: hidden;
    
}

.column img {
    margin: 0px;
    display: block;
    height: 100%;
    left: 0;
    -o-object-fit: cover;
       object-fit: cover;
    position: absolute;
    top: 0;
    width: 100%;
    -webkit-transition: -webkit-transform ease-in-out 300ms;
    transition: -webkit-transform ease-in-out 300ms;
    -o-transition: transform ease-in-out 300ms;
    transition: transform ease-in-out 300ms;
    transition: transform ease-in-out 300ms, -webkit-transform ease-in-out 300ms;
}

/* .column img:hover {   
    cursor: pointer; 
    transform: scale(1.08);
    transition: transform cubic-bezier(0.4, 0, 0.2, 1) 450ms;
    background-color: rgba(255, 255, 255, 0.5);


} */

.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
    background-color: #ff4dff;
    z-index: 1;    
  }

  .column .image:hover .overlay {
    opacity: 0.2;    
  }

  .column .image:hover img {
    cursor: pointer; 
    -webkit-transform: scale(1.08); 
        -ms-transform: scale(1.08); 
            transform: scale(1.08);
    -webkit-transition: -webkit-transform cubic-bezier(0.4, 0, 0.2, 1) 450ms;
    transition: -webkit-transform cubic-bezier(0.4, 0, 0.2, 1) 450ms;
    -o-transition: transform cubic-bezier(0.4, 0, 0.2, 1) 450ms;
    transition: transform cubic-bezier(0.4, 0, 0.2, 1) 450ms;
    transition: transform cubic-bezier(0.4, 0, 0.2, 1) 450ms, -webkit-transform cubic-bezier(0.4, 0, 0.2, 1) 450ms;
    background-color: rgba(255, 255, 255, 0.5);
  }

/* Responsive layout - makes a two column-layout instead of four columns */
@media (max-width: 800px) {
    .column {
        -ms-flex: 50% 1;
            flex: 50% 1;
        max-width: 50%;           
    }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media (max-width: 600px) {
    .column {
        -ms-flex: 100% 1;
            flex: 100% 1;
        max-width: 100%;        
    }
}


